import React from "react";
import { ThemeProvider } from "styled-components";
import theme from "../system/theme";
import Hero from "./index/_Hero";
import CardPage from "./index/_CardPage";
import FormPage from "./index/_FormPage";
import GettingStarted from "./index/_GettingStarted";
import SEO from "../components/seo";

const App = () => (
  <ThemeProvider theme={theme}>
    <>
      <SEO title="Home" />
      <Hero />
      <CardPage />
      <FormPage />
      <GettingStarted />
    </>
  </ThemeProvider>
);

export default App;
